.menu--dark {
  background-color: @chocolate-kisses;
  background-color: black;
}

.menu--dark .menu {
  &__heading {
    color: @desert-sand;
  }

  &__title {
    color: @desert-sand;
  }

  &__desc {
    color: white;
  }

  &__price {
    color: @chocolate-kisses;
    background-color: @desert-sand;
  }
}
