.menu--horizontal .menu {

  @width: 12em;
  @height: 12em;

  &__list {
    grid-template-columns: @width*2 @width*2 @width*2;
    grid-gap: 3em;
  }

  &__item {
    flex-direction: row;
  }

  &__img {
    width: 0.75 * @width;
    height: 0.75 * @height;
  }

  &__txt {
    padding-left: 1rem;
  }

  &__title {
    margin-top: 0;
  }

  &__features-list {
    right: auto;
    left: -1rem;
  }

  &__features-item {
    box-shadow: 2px 2px 3px 0 fade(black, 50%);
  }
}
