.menu {

  @width: 12em;
  @height: 12em;

  &__inner {
    padding-top: 2rem;
    padding-bottom: 2rem;
    display: table;
    margin: 0 auto 0;
    font-size: 16px;
  }

  &__heading {
    font-family: "American Typewriter Bold";
    color: @chocolate-kisses;
    margin: 0;
    padding: 2rem 0;
    font-size: 45px;
    line-height: 3rem;
    // text-transform: uppercase;
  }

  &__list {
    display: grid;
    grid-template-columns: @width @width @width @width @width @width @width;
    grid-gap: 2em;
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;

    // &--hit::before {
    //   content: "hit";
    //   position: absolute;
    //   top: 0;
    //   left: 0;
    //   padding: 0.25rem 0;
    //   z-index: 1;
    //   color: black;
    //   text-align: center;
    //   width: 4em;
    //   background-color: @desert-sand;

    //   border-top-left-radius: 0.25rem;
    //   border-bottom-right-radius: 0.25rem;

    // }
  }

  &__txt {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    line-height: 1rem;
  }

  &__img {
    position: relative;
    height: @height;
    width: @width;
    flex-shrink: 0;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      border-radius: 0.25rem;
    }
  }

  &__title {
    color: @chocolate-kisses;
    margin-top: 1rem;
    margin-bottom: 0;
    font-size: 16px;
    height: 2rem;
    // text-transform: uppercase;
  }

  &__desc {
    color: black;
    margin-top: 0.5rem;
    font-size: 14px;
    flex-grow: 1;
  }

  &__price {
    margin-top: 1rem;
    padding: 0.25rem;
    color: white;
    font-weight: bold;
    text-align: center;
    background-color: @chocolate-kisses;
    border-radius: 0.25rem;
  }

  &__features-list {
    position: absolute;
    top: -1rem;
    right: -1rem;
    display: flex;
  }

  &__features-item {
    font-size: 0;
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    z-index: 1;
    box-shadow: -2px 2px 3px 0 fade(black, 50%);

    svg {
      position: relative;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      filter: invert(1);
    }
  }

  &__features-item ~ &__features-item {
    margin-left: -1.5rem;
  }

  &__features-item--beef {
    background-color: @chocolate-kisses;
  }

  &__features-item--chicken {
    background-color: @desert-sand;

    svg {
      transform: translate(-50%, -50%) scale(-1, 1);
      filter: none;
    }
  }

  &__features-item--pork {
    background-color: @caput-mortuum;
  }

  &__features-item--pepper {
    background-color: @ruby-red;

    svg {
      width: 55%;
    }
  }

  &__features-item--sausages {
    background-color: @indian-yellow;

    svg {
      filter: none;
    }
  }
}
